.friendList {
    display: flex;
    flex-direction: column;
    list-style: none;
    width: 400px;
    padding: 0;
    border-radius: 4px;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
  }
  
  .friendList .item {
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 4px;
    margin: 4px;
    padding: 4px;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
  }
  
  .status {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 15px;
  }
  
  /* .online {
